@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;1,300&display=swap);
body {
  margin: 0;
  padding: 0;
  background: linear-gradient(to top, #44082a, #4a143c, #4c204f, #4a2d63, #433b76, #3a4b89, #2a5c9a, #006ca9, #0083b8, #009ac1, #00b0c5, #14c5c5) fixed;
  font-family: 'Roboto', sans-serif;
} 

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
} 

.welcome_page-container{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    padding: 20px;
    margin: auto;
    margin-top: 200px;
    position: -ms-page;
    background-color: rgba(112, 12, 87, 0.192); 
}

.welcomePage-header{
    text-align: center;
    font-size: 60px;
    color: rgb(73, 5, 58);
}

.submit_button{
    width: 200px;
    height: auto;
    border: none;
    padding: 5px;
    background-color: rgba(5, 19, 100, 0.267);
    font-weight: bold;
    transition: transform .3s;
    font-size: 30px;
}

.submit_button:hover {
    background-color: rgb(39, 23, 83);
    color: white;
    transform: scale(1.5);
}
.userInfoInput{
    width: 300px;
    height: 30px;
    text-align: left;
    padding-left: 5px;
}

.container-profile_form {
    display: flex;
    flex-direction: column;
   
}
  
.profile_form {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    background-color: rgba(112, 12, 87, 0.164); 
    margin: auto;
    margin-top: 150px;
    position: -ms-page;
    padding: 20px;
}

.profile_form-header {
    color: rgb(73, 5, 58);
}
.registrate_button{
    width: 200px;
    height: auto;
    border: none;
    padding: 5px;
    margin-top: 20px;
    background-color: rgb(6, 83, 83);
    color: white;
    font-weight: bold;
    transition: transform .3s;
    font-size: 30px;
}
  
.registrate_button:hover{
    background-color: rgb(10, 48, 48);;
    transform: scale(1.5);
}
.userInfo {
    width: 300px;
    padding: 0;
    margin: 10px;
    font-size: 20px;
    color: rgb(61, 4, 49);
    float: left;
    font-weight: bold;
    font-style: italic;
}
